import CasesSlide from "./CasesSlide/CasesSlide";

import "./Cases.scss";
import "swiper/css";
import "swiper/css/navigation";

import React from "react";
import { useRef } from "react";
import { Link } from "react-router-dom";
import { Swiper, SwiperSlide } from "swiper/react";
import { Navigation } from "swiper/modules";
import SwiperCore from "swiper";

import casesImg from "../../../img/svgicons/tik-tok.svg";
import windowsImg from "../../../img/svgicons/windows-black.svg";
import tiktokImg from "../../../img/svgicons/tik-tok-small.svg";
import cryptoImg from "../../../img/svgicons/crypto.svg";
import seoImg from "../../../img/svgicons/seo.svg";
import plusImg from "../../../img/svgicons/plus.svg";

function Cases() {
  const casesSlides = [
    {
      img: tiktokImg,
      type: "",
      title: "Tiktok ",
      description:
        "     Tiktok.   ,  ,  ,  ,   .",
      link: undefined,
      review:
        "   BAS     ,      ,           .",
      authorimg: undefined,
      author: "",
      slogan: "      .",
    },
    {
      img: cryptoImg,
      type: "",
      title: "NotCoin ",
      description:
        "  BAS      NotCoin    .",
      link: undefined,
      review:
        " 2018     Email ,        150$     BAS.        )",
      authorimg: undefined,
      author: "",
      slogan:
        "     : , airdrop  .",
    },
    {
      img: seoImg,
      type: "SEO  ",
      title: "X-GPTWriter",
      description:
        "   ,        ,      .",
      link: undefined,
      review: undefined,
      authorimg: undefined,
      author: undefined,
      slogan: "       .",
    },
    {
      img: cryptoImg,
      type: "",
      title: "NotCoin ",
      description:
        "  BAS      NotCoin    .",
      link: undefined,
      review:
        " 2018     Email ,        150$     BAS.        )",
      authorimg: undefined,
      author: "",
      slogan:
        "     : , airdrop  .",
    },
  ];

  const prevRef = useRef(null);
  const nextRef = useRef(null);
  return (
    <article className="cases block-dashed-top block-dashed-sides">
      <div className="container">
        <div className="plus top left">
          <img src={plusImg} alt="plus" />
        </div>
        <div className="plus top right">
          <img src={plusImg} alt="plus" />
        </div>

        <main className="cases__row">
          <aside className="cases__aside">
            <div className="cases__arrows">
              <div
                className="swiper-button-prev cases__prev"
                ref={prevRef}
              ></div>
              <div
                className="swiper-button-next cases__next"
                ref={nextRef}
              ></div>
            </div>
            <img src={casesImg} alt="" />
            <label className="label">
              <div className="button-container">
                <Link to="#" className="btn btn-gradient btn-with-image">
                  <img src={windowsImg} />
                  
                </Link>
              </div>
              <p className="label__text"></p>
            </label>
          </aside>
          <Swiper
            className="swiper"
            modules={[Navigation]}
            spaceBetween={24}
            slidesPerView={1}
            navigation={{
              prevEl: prevRef.current,
              nextEl: nextRef.current,
            }}
            breakpoints={{
              768: {
                slidesPerView: 2,
              },
            }}
            onBeforeInit={(swiper) => {
              swiper.params.navigation.prevEl = prevRef.current;
              swiper.params.navigation.nextEl = nextRef.current;
            }}
          >
            {casesSlides.map((slide) => {
              return (
                <SwiperSlide>
                  <CasesSlide
                    img={slide.img}
                    type={slide.type}
                    title={slide.title}
                    description={slide.description}
                    link={slide.link}
                    review={slide.review}
                    authorimg={slide.authorimg}
                    author={slide.author}
                    slogan={slide.slogan}
                  />
                </SwiperSlide>
              );
            })}
          </Swiper>
        </main>
      </div>
    </article>
  );
}

export default Cases;
