import { Link } from "react-router-dom";
import "./Faq.scss";

import FaqItem from "./FaqItem/FaqItem";

function Faq() {
  const faqItems = [
    {
      question: "   BAS   ?",
      asnwer:
        "           .                 .",
    },
    {
      question: "      BAS?",
      asnwer: "  .",
    },
    {
      question: "       ?",
      asnwer:
        "     ,      .       90%.    .",
    },
    {
      question: "      BAS?",
      asnwer: [
        <p>
                  
            .      
                 
          . .      
           
          <Link to="Upwork.com">Upwork.com</Link>
          <Link to="Fiverr.com">Fiverr.com,</Link>
          <Link to="Kwork.ru">Kwork.ru,</Link>
          <Link to="FL.ru">FL.ru.</Link>
        </p>,
      ],
    },
    {
      question: "     BAS?",
      asnwer: "     .",
    },
  ];

  return (
    <article className="faq">
      <div className="container">
        <header className="block-header border-2">
          <h3 className="text-gradient">  ?</h3>
          <h2>
             
            <span></span>
          </h2>
        </header>
        <main className="faq__row">
          {faqItems.map((item, i) => {
            return (
              <FaqItem
                index={++i}
                question={item.question}
                asnwer={item.asnwer}
              />
            );
          })}
        </main>
      </div>
    </article>
  );
}
export default Faq;
